@import url(https://fonts.googleapis.com/css?family=Montserrat);
input {
  width: 5rem;
  border: 2px solid cornflowerblue;
  height: 5rem;
  border-radius: 5px;
  font-size: 3rem;
  font-family: sans-serif;
  text-align: center; }

body {
  background: #5DD39E;
  font-family: 'Pacifico', cursive;
  letter-spacing: 2px;
  font-weight: 100;
  margin-bottom: 30px; }

.button {
  display: inline-block;
  padding: 15px;
  margin-right: 5px;
  height: 50px;
  min-width: 130px;
  background: #348AA7;
  border: none;
  outline: none;
  color: white;
  font-family: inherit;
  font-weight: 400;
  font-size: 20px;
  border-radius: 3px;
  line-height: 1px;
  -webkit-box-shadow: 0 5px 0px #348aa7;
          box-shadow: 0 5px 0px #348aa7;
  border-bottom: 2px solid #30809b; }
  .button .loader-container {
    display: inline-block;
    margin-left: 10px; }
  .button:hover {
    background: #2e7a94;
    -webkit-box-shadow: 0 4px 1px #2e7a94;
            box-shadow: 0 4px 1px #2e7a94;
    border-bottom: 2px solid #2a7088;
    -webkit-transition: all 0.1s ease-in;
    -o-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in; }
  .button:active {
    -webkit-transform: translateY(4px);
        -ms-transform: translateY(4px);
            transform: translateY(4px);
    border-bottom-width: 2px;
    -webkit-box-shadow: none;
            box-shadow: none; }

textarea {
  border: 2px solid cornflowerblue;
  border-radius: 5px;
  font-family: sans-serif;
  width: 90%;
  margin: auto;
  margin-bottom: 10px;
  display: block; }

.rating-helper {
  font-size: 0.7em; }

.notes-container {
  padding: 0px 10px;
  text-align: left;
  border-bottom: 2px solid cornflowerblue; }
  .notes-container .add {
    padding: 10px;
    display: block; }
  .notes-container.text {
    border-top: 2px solid cornflowerblue; }
  .notes-container.sleep .rating {
    display: block;
    margin: auto;
    text-align: center;
    font-family: sans-serif;
    letter-spacing: 0; }
  .notes-container.work {
    margin-bottom: 10px; }
    .notes-container.work .rating {
      display: block;
      margin: auto;
      text-align: center;
      font-family: sans-serif;
      letter-spacing: 0; }

.header {
  height: 4em;
  background: blanchedalmond;
  border-bottom: 2px solid cornflowerblue; }
  .header span {
    text-shadow: 0px 1px 0px cornflowerblue, 0px 2px 0px cornflowerblue, 0px 3px 0px cornflowerblue, 0px 4px 0px cornflowerblue, 0px 5px 0px #aaaaaa, 0px 6px 1px rgba(0, 0, 0, 0.1), 0px 0px 5px rgba(0, 0, 0, 0.1), 0px 1px 3px rgba(0, 0, 0, 0.3), 0px 3px 5px rgba(0, 0, 0, 0.2), 0px 5px 10px rgba(0, 0, 0, 0.25), 0px 20px 20px rgba(0, 0, 0, 0.15); }
  .header span {
    height: 100%;
    display: block;
    padding-top: 10px;
    font-size: 1.5em; }



